import React from 'react';
import ReactDOM from 'react-dom';
import '../../../../components/style.scss';
import ResectAuthor from '../../../../images/img/RESECTAuthor.png';

class Input extends React.Component {

  constructor (){
    super ();
    this.state={
      num1:'',
      total:'',
    }

  }

handlenum1 = (event) => {
  this.setState({
    num1:event.target.value
  })
}


exe = (event) =>{
  event.preventDefault();
  this.setState({total: (25 + (((parseInt(this.state.num1))-3)*10))
  });
}


  render() {
    if (this.state.num1<3){
      this.state.total = 25;

    }


    return (
    <div class="container">
      <div class="field">
        <form onSubmit={this.exe}>
        <div class="control">
          <h1 className="title is-size-5 is-size-4-mobile">Input Total Number of Authors (Includes One Supervisor)</h1>
          <input class="input is-primary" type="text" value={this.state.num1} onChange={this.handlenum1} placeholder = "Total Number of Authors"></input>
        </div>
        <br></br>
        <button class="button is-info is-size-5 is-size-3-mobile" type="submit"><strong>Calculate Number of Cases Needed</strong></button>
        </form>
        <br></br>
        <h1 className="title is-size-3-mobile"> {this.state.total} prospective cases (of which minimum {((((this.state.total)-25)/10)*7)+20} first tumour TURBTs) are needed.  </h1>
          <h1 className="title is-size-3-mobile"> {this.state.total} retrospective cases (of which minimum {((((this.state.total)-25)/10)*7)+20} first tumour TURBTs) are needed. </h1>
          <h1 className="title is-size-3-mobile"> A totoal of {this.state.total*2} cases are needed. </h1>
          <hr></hr>
          <h2 className="subtitle is-size-5-mobile"> <strong>
           Please review the authorship requirement diagram:
          </strong></h2>
          <figure className="media-content is-size-4-mobile">
             <img width="1000" height="auto" src={ResectAuthor}></img>
          </figure>
        <br></br>
      </div>
    </div>
    );
  }
}



export default Input
